@import '/src/scss/shares';

header {
  background: $colorDarkest;
}

.guilds-list {
  position: relative;
  height: 60px;
}

.guilds-list .cur-guild,
.guilds-list .guild-item {
  padding: 10px;
  /* display: flex; */
  align-items: center;
  cursor: pointer;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.guilds-list .cur-guild {
  color: #fff;
  background-color: $colorDarkest;
  transition: all 0.25s ease;
  position: relative;
  z-index: 40;
  height: 100%;
}

.guilds-list .cur-guild:not(.no-guild):hover {
  background-color: #17a2b8;
}
.guilds-list .cur-guild img,
.guilds-list .guild-item img {
  margin-right: 10px;
  width: 40px;
  height: 40px;
}

.guilds-list .guild-item {
  background-color: transparent;
  transition: all 0.25s ease;
}
.guilds-list .guild-item:hover,
.guilds-list .guild-item.current {
  background-color: #f8f9fa;
}

header {
  position: relative;
  z-index: 40;
  min-height: 60px;
}
@media (max-width: 767px) {
  header {
    padding: 20px 0;
  }
}
.guilds-list .guild-list {
  position: absolute;
  z-index: 30;
  background-color: #fff;
  width: 100%;
  transition: all 0.25s ease;
  top: -1000%;
  max-height: 300px;
  overflow-y: auto;
  box-shadow: 0px 0px 5px 0px;
}
@keyframes slideDown {
  from {
    top: -100%;
  }
  to {
    top: 100%;
  }
}
.guilds-list.show .guild-list {
  animation: slideDown 0.5s;
  top: 100%;
}
.navbar-brand img {
  width: 40px;
  height: 40px;
}
