@import '/src/scss/shares';
@import '/src/scss/animations';

body {
  overflow: hidden;
  color: $colorLight;
  background: $colorDarkest;
  @media (max-width: 767px) {
    overflow-y: auto;
  }
}
a {
  color: $linkColor;
  &:hover {
    color: $linkHoverColor;
    text-decoration: none !important;
  }
}
.nav-link {
  &.active {
    color: $linkHoverColor;
    text-decoration: none !important;
  }
}

.form-control {
  color: $colorLight;
  background: $inputsBg;
  outline: none;
  box-shadow: none;
  border-color: $inputsBg;
  &:focus {
    color: $colorLight;
    background: $inputsBg;
    box-shadow: none !important;
    border-color: $inputsBg;
  }
}

.chrome-picker {
  > div:nth-child(2) {
    background: $colorDarkest;
  }
}

.main-wrapper {
  background: $colorDark;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  max-width: 1600px;
  margin: 0 auto;
  -webkit-box-shadow: 0px 0px 22px 0px rgba(202, 202, 202, 0.2);
  -moz-box-shadow: 0px 0px 22px 0px rgba(202, 202, 202, 0.2);
  box-shadow: 0px 0px 22px 0px rgba(202, 202, 202, 0.2);
  .header-plus-content {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
  }
  .main-container {
    padding-left: 0;
    padding-right: 0;
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    > .row {
      flex-grow: 1;
    }
    .content-page {
      padding-right: $marginBig;
      padding-top: $marginBig;
      height: calc(100vh - 60px - 55px);
      overflow-y: scroll;
      overflow-x: hidden;
      @media (max-width: 767px) {
        height: auto;
        padding-left: $marginBig;
        padding-bottom: $marginBig;
      }
    }
  }
}

.main-spinner {
  .spinner {
    width: 4rem;
    height: 4rem;
  }
}
.spinner {
  color: $colorLight !important;
}

.card:not(.sidebar-card),
.list-group {
  overflow: hidden;
  margin-bottom: 30px;
  transition: all 0.25s ease;
  animation: slideDownCard 1s;
}

.card {
  background: $colorDarkest;
  border: none;
  border-radius: 0;
}

.list-group {
  .list-group-item {
    background: $colorDarkest;
    border-color: $colorDarkLighter;
  }
}

.table {
  color: $colorLight;
}
.text-muted {
  color: #828282 !important;
}
