@import '/src/scss/shares';
.user-data {
  .header {
    display: flex;
    align-items: center;
    @media (max-width: 767px) {
      flex-wrap: wrap;
    }
    .image-wrapper {
      overflow: hidden;
      border-radius: 50%;
      width: 80px;
      min-width: 80px;
      margin-right: $marginSmall;
      img {
        width: 100%;
      }
    }
    .username {
      font-size: 20px;
    }
    .buttons {
      flex-grow: 1;
      text-align: right;
      button {
        margin-left: $marginSmall;
      }
      @media (max-width: 767px) {
        margin-top: $marginSmall;
      }
    }
  }
  .data-items-list {
    display: flex;
    flex-wrap: wrap;
    margin-left: -$marginBig;
    margin-right: -$marginBig;
    .item {
      width: 25%;
      padding-left: $marginBig;
      padding-right: $marginBig;
      margin-bottom: $marginBig;
      @media (max-width: 991px) {
        width: 50%;
      }
      @media (max-width: 500px) {
        width: 100%;
      }
      .item-inner {
        background-color: rgb(105, 156, 214);
        border-radius: 5px;
        padding: 10px;
        text-align: center;
        .name {
          margin-bottom: $marginSmall;
          font-size: 18px;
          line-height: 1;
        }
        .value {
          font-size: 25px;
          line-height: 1;
        }
      }
    }
  }
}
