@import '/src/scss/shares';
.nav-link.active {
  text-decoration: underline;
}

.open-menu,
.close-menu {
  margin-left: $marginBig;
  margin-top: $marginBig;
  background: none;
  width: 40px;
  height: 40px;
  border: none;
  img {
    width: 100%;
  }
  display: none;
  @media (max-width: 767px) {
    display: block;
  }
}

.sidebar-wrapper {
  display: flex;
  height: 100%;
  @media (max-width: 767px) {
    position: fixed;
    z-index: 100;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100%;
    display: none;
    &.opened {
      display: flex;
    }
    .close-menu {
      display: block;
      position: absolute;
      top: -15px;
      right: 30px;
      z-index: 110;
    }
  }
}

.sidebar-card {
  @media (max-width: 767px) {
    padding-top: 30px;
  }
}

.guilds-list-wrapper {
  background: $colorDarkest;
  width: 80px;
  text-align: center;
  animation: slideInLeft 1s;
  overflow-x: hidden;

  height: calc(100vh - 60px - 55px);
  @media (max-width: 767px) {
    height: auto;
  }

  .guild-list {
    height: 100%;
    overflow-y: scroll;
    overflow-x: hidden;
    padding-top: $marginSmall;
    -ms-overflow-style: none; /* IE and Edge */
    scrollbar-width: none; /* Firefox */
    &::-webkit-scrollbar {
      display: none;
    }
    .guild-item {
      margin-bottom: 10px;
      text-align: center;
      cursor: pointer;
      opacity: 0.3;

      img {
        width: 60px;
        transition: all 0.25s ease;
      }
      .guild-name {
        display: none;
      }
      &:hover {
        img,
        .no-guild-icon {
          transform: scale(1.2);
        }
      }
      &.current {
        opacity: 1;
      }
      .no-guild-icon {
        width: 60px;
        height: 60px;
        background: $colorLight;
        color: $colorDarkLighter;
        border-radius: 50%;
        margin: 0 auto;
        display: flex;
        justify-content: center;
        align-items: center;
        transition: all 0.25s ease;
      }
    }
  }
}

.sidebar-card {
  background: $colorDarkLighter;
  flex-grow: 1;
}

.list-group-item-action {
  color: $colorLight;
}
.list-group-item-action:focus,
.list-group-item-action:hover {
  color: $colorLight;
}
