@import '/src/scss/shares';
.logs-list {
  .list-group-item {
    display: flex;
    justify-content: space-between;
    @media (max-width: 767px) {
      display: block;
    }
    .left {
      @media (max-width: 767px) {
        display: flex;
        justify-content: space-between;
        margin-bottom: 15px;
      }
      span {
        display: block;
      }
    }
    &.UPDATE_SETTINGS {
      background: #603525;
    }
  }
}
