@import '/src/scss/shares';
.pagination {
  display: block;
  span {
    cursor: pointer;
  }
  .page-item {
    display: inline-block;
    .page-link {
      background: $colorDarkLighter;
      border-color: $colorDark;
      color: $colorLight;
    }
    &.disabled {
      .page-link {
        opacity: 0.5;
      }
    }
    &.active {
      .page-link {
        background: $colorLight;
        color: $colorDarkLighter;
      }
    }
  }
}
