@import '/src/scss/shares';
.card {
  &.chart-card {
    height: 500px;
  }
  &.chart-wrapper {
    @media (max-width: 767px) {
      overflow-x: auto;
    }
  }
}
.card-body {
  &.chart {
    height: 700px;
    @media (max-width: 767px) {
      width: 1400px;
    }
  }
}

.recharts-tooltip-label {
  color: $colorDark;
}
